
/**
 * fetch Fetch API timeoutfetchfetchtimeout
 * fetchtimeout
 *  timeoutresponse
 * fetchtimeoutabort
 * @param {Promise} fetch_promise    fetchPromise
 * @param {number} [timeout=60000]   1fenzhong
 * @return Promise
 */
function timeout_fetch(fetch_promise, timeout = 60000) {
    let timeout_fn = null;

    //rejectpromise
    let timeout_promise = new Promise(function (resolve, reject) {
        timeout_fn = function () {
            reject('');
        };
    });

    //Promise.race resolve  reject 
    let abortable_promise = Promise.race([
        fetch_promise,
        timeout_promise
    ]);

    setTimeout(function () {
        timeout_fn();
    }, timeout);

    return abortable_promise;
}

/**
 * @param {string} url 
 * @param {string} method GETPOST
 * @param {JSON} [params=''] body
 * @param {object} setting 
 * @return Promise
 */
function fetchRequest(path, params = '', setting) {
    let url = path;

    let options = Object.assign({
        credentials: 'include',
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            "Content-Type": "application/json;charset=UTF-8"
        }
    }, setting);

    let paramsArray = [];
    if (params) {//
        Object.keys(params).forEach(key => paramsArray.push(key + '=' + (typeof params[key] == 'string' ? params[key] : JSON.stringify(params[key]))));
    }

    if (options.method.toUpperCase() === 'GET') {
        if (params) {//
            url += (url.search(/\?/) === -1 ? '?' : '&') + paramsArray.join('&');
        }
        console.log(options.method.toUpperCase(), 'request url:', url);  //
    }
    else {
        if (!options.body) {
            if (options.formData) {
                options.body = paramsArray.join('&');
            }
            else {
                options.body = JSON.stringify(params);
            }
        }
        console.log(options.method.toUpperCase(), 'request url:', url, options.body);  //
    }


    return new Promise(function (resolve, reject) {
        let sendTime = new Date();

        if (url.indexOf('http') === -1) {
            console.log('!' + url);
            return;
        }
        timeout_fetch(fetch(url, options))
            .then((response) => {
                console.log('request ' + ((new Date()).getTime() - sendTime.getTime()) / 1000 + '');
                if (response.status === 200) {
                    return response.json();
                }
                else {
                    let text = '', textStatus = response.statusText, status = response.status;
                    if (textStatus === 'timeout') {
                        text = ',';
                    } else if (textStatus === 'error') {
                        text = '[' + response.state + ']';
                    } else if (textStatus === 'parsererror') {
                        text = '';
                    } else if (textStatus === 'abort') {
                        console.log(',');
                    } else if (status === '404') {
                        text = '('+status+')';
                    } else {
                        try {
                            return response.json();
                        }
                        catch (err) {
                            text = '[' + response.status + (textStatus ? '---' + textStatus : '') + ']';
                        }
                    }
                    throw text;
                }
            })
            .then((responseData) => {
                console.log('request success:', url, responseData);   //

                 if(responseData){
                        if (responseData.error) {
                            throw responseData.error_description;
                        }
                        else if (responseData[0] && responseData[0].Code === '999') {
                            throw responseData[0].ShortText;
                        }
                        else {
                            if (responseData.Message) {
                                throw responseData.Message;
                            }
                            else {
                                resolve(responseData);
                            }
                        }
                 }
                 else{
                    resolve(responseData);
                 }
           
            })
            .catch((err) => {
                console.log('request err:', url, err);   //
                reject(err);
            })

    });
}



export default fetchRequest;